// 在页面JS文件顶部添加初始化代码
const db = wx.cloud.database({
    env: 'cloud1-6gprmoiwdc530414' // 必须替换为你的云环境ID（在云开发控制台查看）
  })
  Page({
    data: { recipes: [] },
    onLoad(options) {
      const type = options.type;
      const db = wx.cloud.database();
      db.collection('recipes')
        .where({ cuisine_type: type })
        .get()
        .then(res => {
          console.log('查询结果:', res.data) // 打印数据到控制台
          this.setData({ recipes: res.data })
        })
        .catch(err => {
          console.error('查询失败:', err) // 打印错误信息
        })
    }
  })