Page({
  data: {
    recipe: {}
  },
  onLoad(options) {
    const id = parseInt(options.id);
    const recipes = [
      { id: 1, name: "番茄炒蛋", image: "/images/tomato_egg.jpg", steps: "1. 番茄切块 2. 鸡蛋打散 3. 炒熟即可" },
      { id: 2, name: "红烧肉", image: "/images/braised_pork.jpg", steps: "1. 五花肉焯水 2. 炒糖色 3. 炖煮1小时" }
    ];
    this.setData({ recipe: recipes.find(item => item.id === id) });
  },
  goBack() {
    wx.navigateBack();
  }
});